#root {
  height: 100%;
}

.controls {
  padding: 15px 20px;
  border-bottom: 1px solid #ddd;
}

.console {
  position: sticky !important;
  bottom: 0 !important;
  border-top: 3px solid #aaa;
}

.console-inner {
  width: 100%;
  height: 100%;
  padding: 5px 0 15px;
  overflow-y: auto;
  background-color: #333;
}
